body {
  margin: 0;
  padding: 0;
  background-image: url(/funbox/static/media/Pattern.6bfe3d82.png);
  background-repeat: repeat-y;
  background-size: cover;
}

@font-face {
  font-family: 'Exo2.0';
  src: url(/funbox/static/media/Exo2.0-Thin.44bde874.otf);
}

@font-face {
  font-family: 'Trebuchet MS';
  src: url("/funbox/static/media/Trebuchet MS.48c4ef39.ttf");
}
header h1 {
  z-index: 100;
  font-size: 36px;
  color: #ffffff;
  font-family: "Exo2.0";
  text-align: center;
}

.cards-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.card-footer {
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  font-family: "Trebuchet MS";
  text-align: center;
}

.shadow-back {
  z-index: -2;
  position: absolute;
  width: 100vw;
  height: 100vh;
  margin-top: -24px;
  overflow: hidden;
  background-image: -webkit-linear-gradient(bottom, rgba(0,0,0,0.7) 0%, rgba(225,225,225,0.1) 50%, rgba(0,0,0,0.7) 100%);
  background-image: linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(225,225,225,0.1) 50%, rgba(0,0,0,0.7) 100%)
}
.card-container {
    width: 320px;
    height: 480px;
    border-radius: 12px;
    border-top-left-radius: 0px;
    background-color: #f2f2f2;
    border: 4px solid;
    -webkit-clip-path: polygon(050px 0, 100% 0, 100% 100%, 0 100%, 0 50px);
            clip-path: polygon(050px 0, 100% 0, 100% 100%, 0 100%, 0 50px);
    box-sizing: border-box;
    overflow: hidden;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.top-left-border {
    z-index: 25;
    position: absolute;
    width: 50px;
    height: 50px;
    -webkit-clip-path: polygon(90% 0, 100% 0%, 0% 100%, 0 90%);
            clip-path: polygon(90% 0, 100% 0%, 0% 100%, 0 90%);
}

.disabled-card {
    z-index: 100;
    cursor: not-allowed;
    position: absolute;
    height: 480px;
    width: 320px;
    background-color: rgba(225, 225, 225, 0.5);
    -webkit-clip-path: polygon(50px 0, 100% 0, 100% 100%, 0 100%, 0 50px);
            clip-path: polygon(50px 0, 100% 0, 100% 100%, 0 100%, 0 50px);
    border-radius: 12px;
}

.card-description {
    margin-left: 50px;
    font-size: 16px;
    color: #666666;
    font-family: "Trebuchet MS";
}

.new-card-description {
    margin-left: 50px;
    font-size: 16px;
    color: #e62e7a;
    font-family: "Trebuchet MS";
}

.card-name {
    margin: 0;
    font-size: 48px;
    color: #000000;
    font-family: "Trebuchet MS";
    font-weight: bold;
    margin-left: 50px;
}

.item-name {
    margin-top: 0;
    font-size: 24px;
    color: #000000;
    font-family: "Trebuchet MS";
    font-weight: bold;
    margin-left: 50px;
}

.card-info {
    margin: 0;
    font-size: 14px;
    line-height: 16px;
    color: #666666;
    font-family: "Trebuchet MS";
    margin-left: 50px;
}

.cat-photo {
    cursor: pointer;
    z-index: 20;
    position: absolute;
    background-image: url(/funbox/static/media/Cat-photo.90214c75.png);
    background-position: 60% 175%;
    background-repeat: no-repeat;
    width: 320px;
    height: 480px;
    border-radius: 12px;
}

.card-weight-circle {
    cursor: pointer;
    position: absolute;
    z-index: 25;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-left: 210px;
    margin-top: 370px;
}

.card-weight-circle h1 {
    margin: 0;
    padding-top: 15px;
    font-size: 42px;
    color: #ffffff;
    font-family: "Trebuchet MS";
    text-align: center;
}

.card-weight-circle h2 {
    margin: 0;
    font-size: 25px;
    color: #ffffff;
    font-family: "Trebuchet MS";
    text-align: center;
}

.buyButton {
    cursor: pointer;
    color: #1597d9;
    border-bottom: 1px dashed;
}

.disabled-card-footer {
    font-size: 13px;
    color: #ffff66;
    font-family: "Trebuchet MS";
    text-align: center;
}
