header h1 {
  z-index: 100;
  font-size: 36px;
  color: #ffffff;
  font-family: "Exo2.0";
  text-align: center;
}

.cards-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.card-footer {
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  font-family: "Trebuchet MS";
  text-align: center;
}

.shadow-back {
  z-index: -2;
  position: absolute;
  width: 100vw;
  height: 100vh;
  margin-top: -24px;
  overflow: hidden;
  background-image: linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(225,225,225,0.1) 50%, rgba(0,0,0,0.7) 100%)
}